LODEARS
=====
*/
html, body {
    // overflow-y: hidden;
    overflow-x: hidden;
}

ul {
  margin: 0;
  padding: 0;
}
a {
    text-decoration: none;
    font-family: inherit;
    font-size: inherit;
    color: inherit;
    &:hover, &:focus {
        text-decoration: none;
        color: inherit;
    }
}
button {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
}


.pre-loader {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999;
    background-color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;

    .content {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      width: 230px;
    }

    img {
        // animation: fade-scale-up-down 1.1s infinite alternate;
    }


    @-webkit-keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    
    @keyframes loader {
      0% {
        width: 0;
      }
      20% {
        width: 10%;
      }
      25% {
        width: 24%;
      }
      43% {
        width: 41%;
      }
      56% {
        width: 50%;
      }
      66% {
        width: 52%;
      }
      71% {
        width: 60%;
      }
      75% {
        width: 76%;
      }
      94% {
        width: 86%;
      }
      100% {
        width: 100%;
      }
    }
    .progress-bar {
      background-color: #d3d3d3;
      border: none;
      // border-radius: 60px;
      overflow: hidden;
      width: 200px;
      height: 2px;
      box-shadow: none;
      margin: 42px 0 0 0;
      .Shape_1 {
        background-color: #d3d3d3;
        position: absolute;
        left: 583px;
        top: 459px;
        width: 200px;
        height: 2px;
        z-index: 4;
      }
      
    }
    .progress-bar span {
      display: block;
    }
    
    .bar {
      // background: rgba(0, 0, 0, 0.075);
      background-color: #d3d3d3;
      box-shadow: none;
    }
    
    .progress {
      -webkit-animation: loader 8s ease infinite;
              animation: loader 8s ease infinite;
      background: #958854;
      border: none;
      // border-radius: 50px;
      color: #fff;
      padding: 0px;
      width: 0;
      height: 2px;
    }
    
    .progress-bar {
      // -webkit-transform: rotateY(180deg);
      //         transform: rotateY(180deg);
    }
    
}
@keyframes fade-scale-up-down {
    0% {
        opacity: 0;
        transform: scale(0.987) translateY(-50%);
    }
    100% {
        opacity: 1;
        transform: scale(1.1) translateY(0%);
    }
}





  

 
