.search-container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  background-color: #00000080;
  .close {
    opacity: 0.9;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #958854;
    color: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .zx-search {
    width: 50%;
    height: 50px;
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translateX(-50%);
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      border-radius: 50px;
      padding: 0 20px;
      box-shadow: 0px 0px 10px 0px #95885480;
      &::placeholder {
        font-family: ar-reg;
      }
    }
    button {
      font-size: 18px;
      color: rgb(15, 15, 15);
      position: absolute;
      top: 50%;
      left: 20px;
      transition: all .5s ease-in-out;
      transform: translateY(-50%);
      &:hover {
        color: #958854;
      }
    }
  }
}






/*------ upperbar start ------*/
.upper-bar {
  padding: 8px 0;
  background-color: #958854;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .upper-left, .upper-right {
    display: flex;
    .serv-container {
      font-family: reg;
      font-size: 13px;
      color: rgb(255, 255, 255);
      margin: 0 0 0 20px;
      span {
        padding: 0 5px;
      }
    }
  }
  .upper-right {
    justify-content: flex-end;
    align-items: center;
  }
  .lang-switcher, .cur-switcher {
    position: relative;
    font-family: reg;
    font-size: 14px;
    color: #fff;
    margin: 0 20px 0 0;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    > span {
      cursor: pointer;
      transition: all .3s ease-in-out;
      &::after {
        content: "\f0d7"; /* FontAwesome Unicode */
        font-family: 'Font Awesome\ 5 Free';
        font-weight: 600;
        font-size: 11px;
        color: #fff;
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
    }
    .switch {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 55%;
      z-index: 9;
      transform: translateX(-50%);
      min-width: 110px;
      width: max-content;
      background-color: #fff;
      box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.09);
      color: #000;
      border-radius: 5px;
      padding: 8px 5px;
      transition: all .5s ease-in-out;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      a {
        display: block;
        margin: 3px 0;
        transition: all .3s ease-in-out;
        &:hover {
          color: #958854;
        }
      }
      span {
        padding: 0 2px;
      }
    }
    &:hover {
      > span {
        color: #fff;
        &::after {
          color: #fff;
        }
      }
      .switch {
        top: 23px;
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
/*------ upperbar end ------*/




/*------ navbar start ------*/
.nav-bar {
  box-shadow: 0px 0px 8px 0px rgba(22, 22, 22, 0.1);
  z-index: 9;
  .zx-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
  }
  .container {
    position: relative;
  }
  .logo-container {
    width: 230px;
    .logo {
      width: 186px;
    }
  }
  .nav-search {
    position: relative;
    width: 557px;
    height: 46px;
    background-color: #f5f5f5;
    border: none;
    border-radius: 50px;
    transition: all .3s ease-in-out;
    margin: 20px 0 0 0;
    input {
      width: 100%;
      height: 100%;
      border: none;
      outline: none;
      background-color: transparent;
      padding: 0 20px;
      transition: all .3s ease-in-out;
      &::placeholder {
        font-family: reg;
        font-size: 14px;
        color: #bbbbbb;
      }
      &:hover, &:focus {
        border: 1px solid #958854;
        border-radius: 50px;
      }
    }
    button {
      font-size: 14px;
      color: #131313;
      position: absolute;
      top: 50%;
      left: 7px;
      transform: translateY(-50%);
      transition: all .3s ease-in-out;
      width: 38px;
      height: 38px;
      border-radius: 50%;
      background-color: #958854;
      border: 1px solid #958854;
      color: #fff;
      &:hover {
        background-color: #f5f5f5;
        color: #958854;
      }
    }
  }
  .icons {
    width: 230px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin: 20px 0 0 0;
    .icon-container {
      position: relative;
      font-size: 14px;
      color: #252933;
      margin: 0 0 0 15px;
      &:last-child {
        margin: 0;
      }
      i {
        padding: 0 5px;
        transition: all .3s ease-in-out;
      }
      span {
        font-family: reg;
        transition: all .3s ease-in-out;
      }
      &:hover {
        color: #958854;
      }
    }
    .menu-icon, .search-icon {
      display: none;
    }
    .cart-icon {
      width: 50px;
      height: 50px;
      background-color: #ffffff;
      border-style: solid;
      border-width: 1px;
      border-color: rgb(236, 236, 236);
      border-radius: 50%;
      color: #252933;
      font-size: 16px;
      display: flex;
      justify-content: center;
      align-items: center;
      .counter {
        position: absolute;
        top: -3px;
        right: -3px;
        width: 15px;
        height: 15px;
        padding: 1px 0 0 0;
        color: #ffffff;
        // border-style: solid;
        // border-width: 2px;
        // border-color: rgb(255, 255, 255);
        border-radius: 50%;
        background-color: #958854;
        font-family: reg;
        font-size: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: all .5s ease-in-out;
      }
      &:hover {
        color: #fff;
        background-color: #958854;
        .counter {
          transform: rotateY(360deg);
          background-color: #fff;
          color: #000;
        }
      }
    }
  }
  
}


/*------ navbar end ------*/



/*------ navlist start ------*/
.nav-list {
  width: 100%;
  background-color: #fff;
  padding: 14px 0;
  border-top: 1px solid #f4f4f4;
  .list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
      position: relative;
      font-family: reg;
      font-size: 16px;
      color: #000;
      text-transform: uppercase;
      margin: 0 0 0 20px;
      transition: all .3s ease-in-out;
      &:hover {
        color: #958854;
        .sub-cat-hover {
          opacity: 1;
          visibility: visible;
          top: 30px;
        }
      }
    }
  }

  .sub-cat {
    .sub-cat-hover {
      opacity: 0;
      visibility: hidden;
      position: absolute;
      top: 40px;
      left: 50%;
      transform: translateX(-50%);
      min-width: 140px;
      min-height: 200px;
      background-color: rgb(255, 255, 255);
      box-shadow: 0px 0px 12px 0px rgba(0, 0, 0, 0.1);
      padding: 5px 13px;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-direction: column;
      z-index: 9;
      transition: all .5s ease-in-out;
      li {
        width: 100%;
        transition: all .3s ease-in-out;
        font-family: ar-reg;
        font-size: 16px;
        color: #5e5e5e;
        border-bottom: 1px solid #e6e6e6;
        padding: 7px 0;
        margin: 0;
        text-align: center;
        &:last-child {
          border: none;
        }
        &:hover {
          color: #958854;
        }
      }
    }
  }
}
/*------ navlist end ------*/





/*------ header start ------*/
.header {
    .owl-carousel {
      position: relative;
      .owl-nav {
        display: none;
        .owl-prev, .owl-next {
          outline: none;
          width: 50px;
          height: 50px;
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          font-size: 14px;
          background-color: rgba(0, 0, 0, 0.5);
          // border-radius: 50%;
          color: rgb(255, 255, 255);
          transition: all .5s ease-in-out;
          &:hover {
            color: #1a1a1a;
            background-color: #fff;
          }
        }
        .owl-prev {
          right: 0px;
        }
        .owl-next {
          left: 0px;
        }
      }
      .owl-dots {
        width: max-content;
        position: absolute;
        left: 50%;
        bottom: 30px;
        transform: translateX(-50%);
        .owl-dot {
          width: 9px;
          height: 9px;
          background-color: #00000033;
          border-radius: 50px;
          outline: none;
          margin: 0 4px;
          &.active {
            background-color: #958854;
          }
        }
      }
    }
   
}

/*------ header end ------*/




