/*--- card start ---*/
.card {
  width: 212px;
  min-height: 314px;
  overflow: hidden;
  margin: 0 auto;
  &:hover {
    img {
      transform: scale(1.05);
    }
  }
  .card-img {
    height: 212px;
    border-style: solid;
    border-width: 1px;
    border-color: #f2f2f2;
    margin: 0 0 0px 0;
    overflow: hidden;
    img {
      transition: all .5s ease-in-out;
    }
  }
  .card-body {
    padding: 0 5px;
  }
  .title {
    font-family: reg;
    font-size: 15px;
    color: #252933;
    margin: 20px 0 5px 0;
    padding: 0 0 5px 0;
  }
  .price {
    font-family: e-bold;
    font-size: 16px;
    color: #e72f33;
    margin: 0 0 15px 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    padding: 0 0 5px 0;
    .old {
      font-family: reg;
      font-size: 16px;
      color: #989797;
      text-decoration: line-through;
      margin: 0 0px;
    }
  }
}

